let initialState = {
    friendsData: [
        { id: 1, name: '' },
        { id: 2, name: '' },
        { id: 3, name: '' },
        { id: 4, name: '' },
        { id: 5, name: '' }
    ]
};

const friendsReducer = (state = initialState, action) => {
    return {
        ...state
    }
};

export default friendsReducer;